body {
  margin: 0;
  padding: 0;
  font-family: "FontAwesome";
}
.sidebar {
  width: 200px;
  float: left;
  display: block;
  box-sizing: border-box;
  background: #1f2637;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
}
.add_book_wrapper {
  height: 120px;
  border-bottom: 1px solid #323949;
  display: flex;
  justify-content: center;
  align-items: center;
}
.add_book_btn {
  font-family: "Arial";
  font-weight: bold;
  font-size: 14px;
  background: #f2795a;
  color: #fff;
  border: 0;
  border-radius: 3px;
  width: 160px;
  padding: 15px 0;
  text-align: center;
  position: relative;
  cursor: pointer;
  outline: none;
  &:before {
    content: "";
    font-family: "FontAwesome";
    color: #fff;
    font-size: 16px;
    margin-right: 10px;
  }
  &:hover {
    background: #f58a6e;
  }
  &:active {
    background: #e78369;
  }
}
.sidebar_links {
  margin: 0;
  padding: 0;
  border-bottom: 1px solid #323949;
    li {
      list-style: none;
      &:nth-child(1) a:before {
        content: "";
      }
      &:nth-child(2) a:before {
        content: "";
      }
      &:nth-child(3) a:before {
        content: "";
      }
      &:nth-child(4) a:before {
        content: "";
      }
      &:nth-child(5) a:before {
        content: "";
      }
      a {
        width: 100%;
        padding: 14px 20px;
        display: block;
        color: #78829d;
        font-family: "Arial", "FontAwesome";;
        font-weight: normal;
        font-size: 13px;
        text-decoration: none;
        position: relative;
        box-sizing: border-box;
        &.active {
          background: #15a4fa;
          color: #fff;
        }
        &:before {
          font-family: "FontAwesome";
          font-size: 14px;
          margin-right: 10px;
        }
        &:hover {
          color: #fff;
        }
      }
    }
}
.top_title {
  background: #eef1f7;
  float: left;
  border-bottom: 1px solid #dadfea;
  width: calc(100vw - 200px);
  box-sizing: border-box;
  padding-left: 20px;
  position: fixed;
  left: 200px;
  top: 0;
  height: 68px;
  h1 {
    color: #444c63;
    font-family: "Arial";
    font-weight: bold;
    font-size: 22px;
    margin: 0;
    padding: 25px 0;
    line-height: 17px;
  }
}
.filters_books {
  background: #eef1f7;
  float: left;
  border-bottom: 1px solid #dadfea;
  width: calc(100vw - 200px);
  box-sizing: border-box;
  padding: 15px 20px;
  .book_filter_btn {
    .checker {
      color: #8c97b2;
      font-family: "Myriad Pro", sans-serif;
      font-size: 12px;
      font-weight: normal;
      cursor: pointer;
      padding: 5px 10px;
      border-radius: 15px;
      display: inline-block;
    }
    input {
      display: none;
    }
    input:checked ~ .checker {
      background: #97b3ce;
      color: #fff;
    }
  }
}